export const Inputs = ({
  readerInput,
  readerInput1,
  inputValue,
  handleKey,
  addItem,
  inputValue1,
  edition,
  itemEdit,
}) => {
  return (
    <>
      <label htmlFor="title" className="label">
        Title
      </label>
      <input
        type="text"
        name="title"
        id="book-title"
        className="input"
        value={inputValue}
        onChange={readerInput}
        onKeyDown={handleKey}
        required
      />
      <label htmlFor="author" className="label">
        Author
      </label>
      <input
        type="text"
        name="author"
        id="book-author"
        className="input"
        value={inputValue1}
        onChange={readerInput1}
        onKeyDown={handleKey}
        required
      />

      {!edition ? (
        <button
          type="submit"
          value="Add"
          className="button-submit"
          onClick={addItem}
        >
          Add Book
        </button>
      ) : (
        <button
          type="submit"
          value="Add"
          className="button-finish-edition"
          onClick={itemEdit}
        >
          Finish Edition
        </button>
      )}
    </>
  );
};
