import React, { useState, useEffect } from "react";
import { InputContainer } from "./Input/InputContainer";
import { ContainertTodos } from "./ContainertTodos/ContainertTodos";

export const BookList = () => {
  const [inputValue, setinputValue] = useState("");
  const [inputValue1, setinputValue1] = useState("");
  const [edition, setEdition] = useState(false);

    const readerInput = (e) => {
    setinputValue(e.target.value);
  };

  const readerInput1 = (e) => {
    setinputValue1(e.target.value);
  };

  const getSessionStorageOrDefault = (key, defaultValue) => {
    const stored = sessionStorage.getItem(key);
    if (!stored) {
      return defaultValue;
    }
    return JSON.parse(stored);
  };
  const [itemTodo, setItemTodo] = useState(
    getSessionStorageOrDefault("itemTodo", [])
  );

  useEffect(() => {
    sessionStorage.setItem("itemTodo", JSON.stringify(itemTodo));
  }, [itemTodo]);

  const itemDelete = (id) => {
    const newList = itemTodo.filter((item) => item.id !== id);
    setItemTodo(newList);
  };

  const itemCompleted = (id) => {
    const newList = itemTodo.map((item) =>
      item.id === id ? { ...item, completed: !item.completed } : item
    );

    setItemTodo(newList);
  };

  const itemEdit = (id) => {
    setEdition(!edition);
    console.log(id);
    const bookListFiltered = itemTodo.filter((item) => item.id !== id);
    setItemTodo(bookListFiltered);
  };

  const closeEdtion = (id) => {
    setEdition(!edition);
  };

  return (
    <div>
      <InputContainer
        inputValue={inputValue}
        inputValue1={inputValue1}
        itemTodo={itemTodo}
        setinputValue={setinputValue}
        setinputValue1={setinputValue1}
        setItemTodo={setItemTodo}
      />
      <ContainertTodos
        itemTodo={itemTodo}
        itemCompleted={itemCompleted}
        itemDelete={itemDelete}
        itemEdit={itemEdit}
        edition={edition}
        closeEdtion={closeEdtion}
        inputValue={inputValue}
        inputValue1={inputValue1}
        setinputValue={setinputValue}
        setinputValue1={setinputValue1}
        setItemTodo={setItemTodo}
        readerInput={readerInput}
        readerInput1={readerInput1}
      />
    </div>
  );
};
