import React from "react";
import TodoItems from "../TodoItems/TodoItems";
import { Inputs } from "../Input/Inputs";

export const ContainertTodos = ({
  itemTodo,
  itemDelete,
  itemCompleted,
  itemEdit,
  closeEdtion,
  edition,
  readerInput,
  readerInput1,
  inputValue,
  inputValue1,
  setinputValue,
  setinputValue1
}) => {
  return (
    <div>
      <div className={!edition ? "edition-notDisplay" : "edition-display"}>
        <button id="button-edition-close" onClick={closeEdtion}>
          Close
        </button>
        <h1>Please Edit Your book</h1>
        <Inputs edition={edition} 
          itemEdit={itemEdit}     
          readerInput={readerInput}
          readerInput1={readerInput1}
          inputValue={inputValue}
          inputValue1={inputValue1}
          setinputValue={setinputValue}
          setinputValue1={setinputValue1}/>
      </div>
      <ul className="container-todos">
        {itemTodo.map((itemTodo, i) => {
          return (
            <TodoItems
              key={i}
              itemTodo={itemTodo}
              itemDelete={itemDelete}
              itemCompleted={itemCompleted}
              itemEdit={itemEdit}
              edition={edition}
            />
          );
        })}
      </ul>
    </div>
  );
};
